import React from 'react';
import './App.css';

import ContentCard from './ContentCard';
import { Navbar } from 'react-bootstrap';

class LandPriceCard extends ContentCard {
  constructor() {
    super();
    this.state = { data: [] };
  }

  async componentDidMount() {
    try {
      const response = await fetch(`https://api.jonghyunho.com/v1/chartGetHousePrice`);
      const json = await response.json();
      this.setState({ data: json });
    } catch (error) {
      console.log(error);
    }
  }

  getValues() {
    var indices = [];
    var values = [];

    var result = this.state.data;

    for (var index in result.prices) {
      indices.push(result.prices[index][0]);
      values.push(result.prices[index][1]);
    }

    return [indices, values];
  }
}

function App() {
  return (
    <div className="App">
      <header className="App-header">
        <Navbar expand='lg'>
          <Navbar.Brand href="#"><h4>잡동사니</h4></Navbar.Brand>
        </Navbar>
        <LandPriceCard title='서울 부동산 매매가격지수' text='서울 부동산 매매 가격의 등락을 시각화한다.'></LandPriceCard>
        <ContentCard title='경제 지표의 상관관계' text='경제 지표를 상관관계지수를 통해 시각화한다.'></ContentCard>
        <ContentCard title='주식과 부동산' text='주식과 부동산 어느 것이 투자에 더 유리한가?'></ContentCard>
      </header>
    </div>
  );
}

export default App;
